import React, { FC } from 'react';
import { PolygonProps } from './Polygon.types';
import { draw } from '../../svg/svg.methods';
import { ngon, rad } from '../../geometry';

export const Polygon: FC<PolygonProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fillColor = '#000',
	strokeColor = '#000',
	lineWidth = 1,
	strokeAlignment = 'inner'
}) => {
	const [points, viewBox] = ngon(sides, scale, rad(rotation), cornerRadius, [0, 0]);

	return (<svg xmlns="http://www.w3.org/2000/svg" viewBox={viewBox.join(' ')} width="100px" height="100px">
		<path d={draw(points)} fill={fillColor} stroke={strokeColor} strokeWidth={lineWidth} stroke-alignment={strokeAlignment}/>
	</svg>)
}